.btn-item {
    padding: 8px;
    margin-left: 25px ;
    cursor: pointer;
    vertical-align: middle;
    color: #FFFFFF;
    border: none;
    outline: none;

}

.edit {
    background-color: #c2e80e;
    border-radius: 4px;
}

.delete{
    background-color: #18a0fb;
    border-radius: 4px;
}

.trash:hover{
    background-color: #388AC5;

}



.item-list {
    list-style-type: none;
    background-color: #FFFFFF;
    width: 100%;
    padding: 25px 40px;
    margin-top: 40px ;
    margin-bottom: 32px;
    border: 4px solid black;
    border-radius: 10px;



}
.item-list:first-child{
    margin-top: 0;
}

